import React from "react";
import { Bar } from "react-chartjs-2";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
} from 'chart.js';

//  ChartJS 
ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);

const Chart = ({ stats }) => {
  //  labels  1  6
  const labels = ['1', '2', '3', '4', '5', '6'];

  //  stats 
  const data = {
    labels: labels,
    datasets: [
      {
        label: "",
        data: [
          stats[1] || 0,
          stats[2] || 0,
          stats[3] || 0,
          stats[4] || 0,
          stats[5] || 0,
          stats[6] || 0,
        ],
        backgroundColor: "rgba(75, 192, 192, 0.6)",
        borderColor: "rgba(75, 192, 192, 1)",
        borderWidth: 1,
      },
    ],
  };

  const options = {
    responsive: true,
    plugins: {
      legend: {
        position: "top",
      },
      title: {
        display: true,
        text: '',
        position: 'bottom',
        padding: {
          bottom: 10
        },
        font: {
          size: 16,
          weight: 'normal'
        }
      },
    },
    scales: {
      y: {
        beginAtZero: true
      }
    }
  };

  return <Bar data={data} options={options} />;
};

export default Chart;
