import React, { useState, useEffect } from 'react';
import io from 'socket.io-client';
import Chart from './Chart';
import PieChart from './PieChart';
import './App.css';

const SOCKET_URL = 'http://172.20.10.12:5001';

// 
function getDefectDescription(category) {
  const defectDescriptions = {
    0: "normal",
    1: "void",
    2: "horizontal defect",
    3: "vertical defect",
    4: "edge defect",
    5: "particle",
  };

  return defectDescriptions[category] || ""; // 
}

function App() {
  const [stats, setStats] = useState({});
  const [currentImage, setCurrentImage] = useState(null);
  const [currentCategory, setCurrentCategory] = useState(null);
  const [currentSaliencyMap, setCurrentSaliencyMap] = useState(null); // 
  const [defectDescription, setDefectDescription] = useState(""); // 
  const [isImageReceived, setIsImageReceived] = useState(false); // 

  useEffect(() => {
    const socket = io(SOCKET_URL);

    socket.on('connect', () => {
      console.log('WebSocket ');
    });

    socket.on('update_data', (data) => {
      console.log(':', data);
      setStats(data.stats);
      setCurrentImage(data.image);
      setCurrentCategory(data.category);
      setCurrentSaliencyMap(data.saliency_map); //  Saliency Map
      setDefectDescription(getDefectDescription(data.category)); // 
      setIsImageReceived(true); // 
    });

    socket.on('update_stats', (data) => {
      console.log(':', data);
      setStats(data);
    });

    socket.on('disconnect', () => {
      console.log('WebSocket ');
    });

    return () => {
      socket.disconnect();
    };
  }, []);

  // 
  const hasData = Object.keys(stats).length > 0;

  return (
    <div className="App">
      <header className="App-header">
        <h1></h1>

        <div className="content-container">
          {/*  Saliency Map  */}
          <div className="image-saliency-container">
            {/*  */}
            <div className="image-section" style={{ height: isImageReceived ? '400px' : 'auto' }}>
              <h2></h2>
              {currentImage ? (
                <div className="image-container">
                  <img 
                    src={`data:image/jpeg;base64,${currentImage}`}
                    alt=""
                    style={{ maxWidth: '300px' }}
                  />
                  <p className="category-result">: {currentCategory}</p>
                  <p className="defect-description">: {defectDescription}</p>
                </div>
              ) : (
                <p>...</p>
              )}
            </div>
            {/* Saliency Map  */}
            <div className="saliency-map-section" style={{ height: isImageReceived ? '400px' : 'auto' }}>
              <h2></h2>
              {currentSaliencyMap ? (
                <div className="image-container">
                  <img
                    src={`data:image/png;base64,${currentSaliencyMap}`}
                    alt=""
                    style={{ maxWidth: '300px' }}
                  />
                </div>
              ) : (
                <p>...</p>
              )}
            </div>
          </div>

          {/*  */}
          <div className="charts-container">
            {/*  */}
            <div className="stats-section">
              <h2></h2>
              {hasData ? (
                <div style={{ width: '100%', height: '300px' }}>
                  <Chart stats={stats} />
                </div>
              ) : (
                <p>...</p>
              )}
            </div>

            {/*  */}
            <div className="progress-section">
              <h2></h2>
              {hasData ? (
                <div style={{ width: '300px', height: '300px' }}>
                  <PieChart stats={stats} totalTarget={20} />
                </div>
              ) : (
                <p>...</p>
              )}
            </div>
          </div>
        </div>
      </header>
    </div>
  );
}

export default App;
